import { Graph } from "@antv/x6";
import { Snapline } from "@antv/x6-plugin-snapline";
import { Stencil } from "@antv/x6-plugin-stencil";
import { useMount } from "ahooks";
import { useRef } from "react";
import { DATA } from "./data";

export const commonAttrs = {
	body: {
		stroke: "#8f8f8f",
		strokeWidth: 1,
		fill: "#fff",
	},
	img: {
		"xlink:href":
			"https://gw.alipayobjects.com/zos/antfincdn/FLrTNDvlna/antv.png",
		width: 16,
		height: 16,
		x: 12,
		y: 12,
	},
	line: {
		stroke: "#d5d5d5",
		strokeWidth: 1,
		targetMarker: {
			name: "block", // 
			args: {
				size: 5, // 
				attrs: {
					fill: "#d9d9d9", // 
				},
			},
		},
	},
};
Graph.registerNode(
	"custom-node-width-port",
	{
		width: 120,
		height: 40,

		markup: [
			{
				tagName: "rect",
				selector: "body",
			},
		],
		attrs: {
			body: {
				stroke: "#bfa",
				strokeWidth: 1,
				fill: "#fff",
				rx: 0,
				ry: 0,
			},
		},
		ports: {
			groups: {
				top: {
					position: "top",
					attrs: {
						circle: {
							magnet: true,
							stroke: "#8f8f8f",
							r: 5,
						},
					},
				},
				bottom: {
					position: "bottom",
					attrs: {
						circle: {
							magnet: true,
							stroke: "#8f8f8f",
							r: 5,
						},
					},
				},
			},
		},
	},
	true
);

export const Example = () => {
	const container = useRef<HTMLDivElement | null>(null);
	const stencilContainer = useRef<HTMLDivElement | null>(null);

	useMount(() => {
		// 
		const graph = new Graph({
			container: container.current!, // 
			background: {
				color: "#F2F7FA", // 
			},
		});

		// 
		const stencil = new Stencil({
			title: "",
			target: graph,
			search(cell, keyword) {
				return cell.shape.indexOf(keyword) !== -1;
			},
			placeholder: "",
			notFoundText: "",
			// collapsable: true,
			stencilGraphHeight: 0,
			layoutOptions: {
				columns: 1,
			},
			groups: [
				{
					name: "group1",
					title: "",
				},
			],
		});

		// 
		graph.fromJSON(DATA);

		/*  */
		// 
		graph.use(
			new Snapline({
				enabled: true, // 
				sharp: true, // 
			})
		);

		// Stencil
		stencilContainer.current?.append(stencil.container);

		// 
		graph.centerContent();

		// 
		const stencilLabelData = ["K8S", "SQL", "SPARK", "FLINK"];
		// 
		const stencilNodesData = stencilLabelData.map((label) =>
			graph.createNode({
				shape: "custom-node-width-port",
				x: 40,
				y: 40,
				rx: 1,
				ry: 1,
				label: label,
				attrs: commonAttrs,
			})
		);
		// Stencil
		stencil.load(stencilNodesData, "group1");

		/*  */
		// 
		graph.on("node:added", ({ node, options }) => {
			//
			if (options.stencil) {
				node.addPorts([
					{
						id: "stencil_port_1",
						group: "top",
					},
					{
						id: "stencil_port_2",
						group: "bottom",
					},
				]);
			}
		});

		// 
		graph.on("edge:added", ({ edge, options }) => {
			edge.attr({
				line: commonAttrs.line,
			});
		});
	});

	return (
		<div className="stencil-app">
			<div className="app-stencil" ref={stencilContainer} />
			<div className="app-content" ref={container} />
		</div>
	);
};
